.App {
	height: 100vh;
	text-align: center;
	display: flex;
	align-items: center;
	flex-direction: column;
	justify-content: center;
	background: radial-gradient(white, black);
}
.btn-play:hover {
	cursor: pointer;
	background-color: black;
	color: white;
	font-weight: bold;
}
.btn-play {
	border-width: 0;
	width: 100px;
	padding: 10px;
	margin-top: 10px;
	border-radius: 4px;
	background: transparent;
	border: 1px solid black;
}
.input-text {
	height: 30px;
	text-align: center;
	width: 300px;
	font-size: 20px;
	outline: none;
	border: 1px solid black;
	border-radius: 4px;
}
.input-text:focus {
	border-top: 1px solid black;
	border-right: 1px solid black;
	border-left: 1px solid black;
	border-bottom: 2px solid black;
}
.range-input-container {
	margin-top: 10px;
	display: flex;
	justify-content: space-between;
}
.label {
	margin-right: 10px;
	font-size: 15px;
	font-weight: bold;
	width: 150px;
	text-align: right;
}
@media screen and (-webkit-min-device-pixel-ratio: 0) {
	input[type="range"] {
		overflow: hidden;
		width: 100px;
		-webkit-appearance: none;
		background-color: white;
		border-radius: 8px;
	}

	input[type="range"]::-webkit-slider-runnable-track {
		height: 15px;
		-webkit-appearance: none;
		color: white;
		border-radius: 8px;
	}

	input[type="range"]::-webkit-slider-thumb {
		width: 15px;
		-webkit-appearance: none;
		border-radius: 50%;
		height: 15px;
		cursor: pointer;
		background: red;
		box-sizing: border-box;
		box-shadow: -100px 0 0 100px rgb(51, 47, 47);
	}
}
